body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html {
    padding: 0;
    margin: 0;
}
header {
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin: 20px auto;
    align-items: center;
}
header > .image-wrapper {
    max-width: 150px;
    display: block;
}
header > h1 {
    margin-left: 20px;
    font-size: 34px;
}
header > .image-wrapper > img {
    border: none;
}
header > .image-wrapper:hover > img {
    box-shadow: none;
}
* {
    font-family: Helvetica;
    padding: 0;
    margin: 0;
}
ul {
    margin: 10px 0 0;
    /* height: 0; */
    overflow: hidden;
}
li {
    margin: 2px 0 2px 20px;
}
.card {
    border: 1px solid #e3e3e3;
    background:  -webkit-linear-gradient(top, rgba(252,234,187,1) 0%, rgba(252,205,77,1) 50%, rgba(248,181,0,1) 100%);
    background:  linear-gradient(to bottom, rgba(252,234,187,1) 0%, rgba(252,205,77,1) 50%, rgba(248,181,0,1) 100%);
    box-shadow: 8px 8px 10px 0 #bbc0be;
    width: 20%;
    min-width: 250px;
    margin: 20px;
    padding: 20px;
    box-sizing: border-box;
}

.card:hover {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
    -webkit-transform-origin: bottom center;
            transform-origin: bottom center;
    -webkit-transition: -webkit-transform .1s ease-in-out;
    transition: -webkit-transform .1s ease-in-out;
    transition: transform .1s ease-in-out;
    transition: transform .1s ease-in-out, -webkit-transform .1s ease-in-out
}

.card.active {
    box-shadow: 0 0 10px 2px #38258f;
}

.card > h2 {
    text-align: center;
    margin-bottom: 12px;
}
.ingredients-title {
    text-align: center;
    font-size: 14px;
    cursor: pointer;
    margin-bottom: 10px;
    font-weight: 600;
}
.ingredients-title:hover {
    color: #666666;
    font-weight: bold;
}
.ingredients {
    height: 0;
    overflow: hidden;
    font-style: italic;
    font-size: 12px;
}
.ingredients.active {
    min-height: 50px;
    height: auto;
}
.image-wrapper {
    box-sizing: border-box;
    margin-bottom: 10px;
}
.image-wrapper > img {
    border: 1px solid #e3e3e3;
    font-size: 0;
    width: 100%;
}
.image-wrapper:hover > img {
    box-shadow: 0 0 10px 0 #123425;
}
.toggle {
    margin-bottom: 10px;
    text-align: right;
}
.toggle > span {
    padding: 5px 10px;
    cursor: pointer;
    font-size: 14px;
}
.toggle > span > i {
    margin-left: 5px;
}
.toggle:hover > span > i {
    color: rgb(255, 174, 0);
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
}
.info {
    display: flex;
    justify-content: space-around;
    margin-bottom: 20px;
}
.info > * {
    line-height: 1.5;
    text-align: center;
}
.info > * > span {
    display: block;
    font-weight: 600;
}

.all-recipes {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}
.container {
    display: flex;
}
.ingredient-list {
    text-align: center;
    min-width: 300px;
    margin: 0 20px;
}
.ingredient-list li {
    text-align: left;
}
.ingredient-list> h2 {
    margin-bottom: 20px;
}
.selected-recipes {
    margin-bottom: 20px;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

